body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Poppins', sans-serif;
}


.container {
    display: flex;
    flex-direction: column;
}

.featureContainer {
    background-color: #B10058;
    height: 600px;
    width: 100%;
    color: white;
}

.featurePhoto {
    width: 100%;
    height: 100%;
    margin-top: 100px;
    margin-right: 80px;
    object-fit: contain;
    display: block;
    position: relative;
}

.featureParagraph {
    padding-top: 150px;
}

.superLargeText {
    font-size: 52px;
    margin-bottom: 20px;
    font-weight: 500;
}

.largeText {
    font-size: 36px;
    margin-bottom: 32px;
    font-weight: bold;
   
}

.headLine {
    font-size: 24px;
    margin-bottom: 12px;
    font-weight: bold;
    color: white;
    text-decoration: none;

}

a .headLine {
    text-decoration: none;
    color:white;
}
a .headLine :hover {
    text-decoration: none;
    text-decoration: white;
    color: white;
}

.normalText {
    font-size: 24px;

    font-weight: 200;
}

.productCard {
     display: block;
     justify-content: center;
     align-items: center
}

.row .productCard {
    width: 280px;
    height: 420px;
    background-color: white;
    margin-top: 12px;
    margin-left: 12px;
    margin-bottom: 12px;
    margin-right: 12px;

    border-radius: 10px;
    padding-top: 16px;
    padding-left: 16px;
    padding-right: 16px;
    padding-bottom: 30px;
     box-shadow: 0px 1px 5px #aaaaaa;;

    /* align-content: center;
    position: relative; */
}


.productCardImage {
    width: 240px;
    height: 240px;
    padding-bottom: 20px;
    object-fit: cover;
    display: block;
    border-radius: 10px;
    pointer-events: none;
}

.productCardTitle {
    height: 58px;
    padding-top: 4px;
    font-size: 18px;
    padding-bottom: 16px;
    font-family: 'Poppins', sans-serif;
    max-lines: 2;
    overflow: hidden;
}

.productCardPrice {
    width: 240px;
    padding-top: 20px;
    font-size: 22px;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    color: #B10058;
    text-align: left;

}

.productListContainer {
   min-height: 100px;
   align-items: center;
    margin-right: 50px;
    margin-left: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

button.btn {
    background-color: #B10058;
    border-color: transparent;
    align-self: center;
    height: 50px;
     margin: 0px;
}

.link:hover {
    color: black;
    font-weight: bolder;
}


.cartPageContainer {
    padding-top: 120px;
    padding-left: 20%;
    padding-right: 20%;
    min-height: 1200px;
    width: 100%;
}

.cartItemListContainer {
    padding-top: 60px;
    width: 100%;

}

.cartItem {
    height: 100px;
    margin-top: 16px;
    margin-bottom: 16px;
}

.cartItemImage {
    width: 60px;
    height: 60px;
    object-fit: cover;
    display: block;
    border-radius: 4px;
    pointer-events: none;
}


.pinkNav {
    background-color: #B10058;
    color: white;
    text-decoration-color: white;
    padding-left: 60px;
    padding-right: 60px;
}

a.navbar-brand {
    color: white!important;
}

 .navbar-light .navbar-nav a.nav-link {
    color: white;
}

 .navbar-light .navbar-nav a.nav-link:hover {
    color: #ffffff33;
}

.quantityInput input {
    width: 50px;
    text-align: center;
}